    next() {
        if (this.state.current < this.state.helpArr.length - 1) {
            let newCur = this.state.current + 1;
            this.setState({ current: newCur });
        }
    }

    prev() {
        if (this.state.current > 0) {
            let newCur = this.state.current - 1;
            this.setState({ current: newCur });
        }
    }

